import * as Tone from "tone";

export class Player {
  static synth: Tone.Synth<Tone.SynthOptions>;

  public static playNotes(notes: string[], noteDurations: string[]) {
    if (!Player.synth) {
      Player.synth = new Tone.Synth().toDestination();
    }

    if(notes.length !== noteDurations.length){
      throw new Error("Player:playNotes notes and notesDuration do not have the same length")
    }

    let playAt = Tone.now();
    for (let i = 0; i < notes.length; i++) {
      Player.synth.triggerAttackRelease(notes[i], noteDurations[i], playAt);
      playAt += Tone.Time(noteDurations[i]).toSeconds();
    }
  }
}
